@import './variables.scss';

body {
  margin: 0;
  padding: 0;
}

.container {
  width: 100%;
  height: 100vh;
  text-align: center;
  background: linear-gradient($light-blue, $dark-blue);
  @media (min-width: 768px) {
    padding-top: 100px;
  }
}

.h1 {
  margin: 0;
  padding: 20px 0;
}

.display {
  margin: 15px auto;
  padding: 20px;
  width: 200px;
  min-height: 25px;
  border: 1px solid $white;
  border-radius: 4px;
  font-size: 20px;
}

.btn-container {
  padding-top: 20px;
  width: 100%;
  text-align: center;
  .btn {
    margin: 10px;
    width: 70px;
    height: 70px;
    background-color: transparent;
    border-radius: 50%;
    outline: none;
    font-size: 20px;
  }
}

.hidden {
  display: none;
}


